.formControls div {
    display: inline-block;

}
.formControls textarea,
.formControls input
{
    border: #d63a51 solid 1px;
}
.formControls span {
    margin-left: 5px;
    color: #e84848f7;
    font-size: 18px;
}
.formControls__error_text {
    border: 1px #e84848f7 solid;
}

.error {
    padding: 5px 5px 15px 0px;
    color: #e84848f7;
}

.field {
    display: inline-block;

}